<template>
<div class="registerContainer">
  <div class="registerBox">
    <h4> Try </h4>
    <el-form :model="registerForm" status-icon :rules="registerFormRules" ref="registerForm"  class="registerForm">
      <el-form-item  prop="nickName">
        <el-input type="text" placeholder=""  v-model.trim="registerForm.nickName" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item  prop="email">
        <el-input type="email" placeholder=""  v-model.trim="registerForm.email" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" placeholder=""  v-model.trim="registerForm.password" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item prop="checkPass">
        <el-input type="password" placeholder=""  v-model.trim="registerForm.checkPass" autocomplete="off"></el-input>
      </el-form-item>
      <div class="law">
        <el-checkbox v-model="agree"></el-checkbox>
        <span style="padding-left: 4px">
          <a href="javascript:;" @click="showLaw">
           </a>
           <a href="javascript:;" @click="showPrivacy">
           </a>
        </span>
      </div>
      <el-form-item>
        <div class="border">
          <el-button type="primary" @click="upRegister"></el-button>
        </div>
      </el-form-item>
      <div class="ifHave"> Try  <a href="javascript:;" @click="toLogin"></a></div>
    </el-form>
  </div>
</div>
</template>

<script>
import { getAllUsers } from '@/api'

export default {
  name: 'register',
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else {
        if (this.registerForm.password !== '') {
          this.$refs.registerForm.validateField('checkPass')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.registerForm.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    var checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    }
    return {
      registerForm: {
        email: '',
        nickName: '',
        password: '',
        status: 1,
        role: 'normal',
        checkPass: ''
      },
      registerFormRules: {
        password: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: ['blur', 'change'] }
        ],
        nickName: [
          { required: true, message: '', trigger: ['blur', 'change'] },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      },
      agree: false
    }
  },
  methods: {
    upRegister () {
      this.$refs.registerForm.validate(async v => {
        if (!v) return
        if (!this.agree) return this.$message.warning('')
        delete this.registerForm.checkPass
        const { data: res } = await this.$http.post(getAllUsers, this.registerForm)
        if (!res._id) return this.$message.error(res.message)
        this.$message.success(',')
        this.$router.push({ name: 'login' })
      })
    },
    showLaw () {
      this.$notify({
        title: '',
        message: '',
        type: 'info'
      })
    },
    showPrivacy () {
      this.$notify({
        title: '',
        message: '',
        type: 'info'
      })
    },
    toLogin () {
      this.$router.push({ name: 'login' })
    }
  }
}
</script>

<style lang="less" scoped>
.registerContainer{
  width: 100%;
  height: 100%;
  background: linear-gradient(to left,#4ECDC4,#556270);
  .registerBox {
    width: 450px;
    height: 560px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);
    border-radius: 7px;
    box-shadow: 0 0 7px #ddd;
    h4{
      padding: 0;
      text-align: center;
      color: #666;
      border-bottom: 1px solid #dcdcdc;
      box-shadow: none;
      font-weight: 700;
      font-size: 20px;
      height: 60px;
      line-height: 60px;
    }
    .registerForm {
      width: 100%;
      position: absolute;
      top: 100px;
      padding: 0 40px 22px;
      height: auto;
      box-sizing: border-box;
      .border{
        margin-top: 30px;
        padding-bottom: 30px;
        border-bottom: 1px solid #ccc;
        .el-button{
          width: 100%;
          font-size: 18px;
        }
      }
      .law,.ifHave{
        color: #999;
        font-size: 13px;
        a{
          color: #5079d9;
        }
      }
      .ifHave{
        padding-top: 5px;
        text-align: center;
      }
    }
  }
}
</style>
